.help {
  background: $secondary;
  padding: 64px 0;
  @media (max-width: $breakpoint576) {
    padding: 44px 0;
  }
  &__inner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    z-index: 1;
    @media (max-width: $breakpoint1200) {
      justify-content: center;
      gap: 50px;
    }
    @media (max-width: $breakpoint768) {
      flex-direction: column;
      justify-content: stretch;
    }
  }
  &__line {
    background: #00f0ff;
    width: 146px;
    height: 696px;
    border-radius: 0px 0px 100px 100px;
    position: absolute;
    left: 81px;
    top: -64px;
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 109px;
      left: -25px;
      width: 163px;
      height: 865px;
      background: $secondary;
    }
    &:before {
      z-index: -1;
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
    @media (max-width: $breakpoint1200) {
      display: none;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
  }

  &__left {
    width: 100%;
    max-width: 317px;
    z-index: 1;
    @media (max-width: $breakpoint768) {
      max-width: none;
    }
  }

  &__left-title {
    font-weight: 700;
    font-size: 54px;
    line-height: 58px;
    letter-spacing: 0.01em;
    color: $white;
    margin-bottom: 18px;
    @media (max-width: $breakpoint576) {
      font-size: 44px;
      line-height: 48px;
    }
  }

  &__left-descr {
    letter-spacing: 0.01em;
    color: $white;
    margin-bottom: 18px;
    line-height: 26px;
  }

  &__item-tg {
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 26px;
    letter-spacing: 0.01em;
    color: $white;
    svg {
      margin-right: 8px;
      path {
        fill: white;
      }
    }
  }

  &__right {
    max-width: 492px;
    width: 100%;
    margin: 0 104px 0 90px;
    padding: 28px 28px 49px;
    background: $white;
    border-radius: 20px;
    @media (max-width: $breakpoint1200) {
      margin: 0;
    }
    @media (max-width: $breakpoint768) {
      max-width: inherit;
    }
    .help__item-tg {
      margin-bottom: 18px;
      color: $secondary;
      svg {
        path {
          fill: #3589d6;
        }
      }
    }
  }

  &__right-title {
    margin-bottom: 19px;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: 0.01em;
  }

  &__right-btn {
    max-width: inherit;
  }
}

