.why {
  padding: 84px 0 80px 0;

  &__bg-telegram {
    position: absolute;
    top: -58px;
    right: calc(100% - 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 58px solid rgba(0, 240, 255, 0.1);
    @media (max-width: 1600px) {
      z-index: -1;
      right: 300px;
      height: 168px;
      border: 38px solid rgba(0, 240, 255, 0.1);
      svg {
        width: 100px;
      }
    }
    @media (max-width: $breakpoint1200) {
      top: 50%;
      transform: translateY(-50%);
      right: calc(100% - 100px);
    }
    @media (max-width: $breakpoint992) {
      display: none;
    }
  }

  &__inner {
    display: flex;
    position: relative;
    @media (max-width: $breakpoint1200) {
      flex-direction: column-reverse;
      align-items: center;
    }
  }

  &__col {
    width: 100%;
    max-width: 968px;
  }
  &__col-img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 335px;
    width: 100%;
    @media (max-width: 1600px) {
      max-width: 280px;
    }
    @media (max-width: $breakpoint1200) {
      position: static;
      max-width: 520px;
      margin-bottom: 10px;
    }

    img {
      width: auto;
      max-width: 520px;
      @media (max-width: 1600px) {
        max-width: 370px;
      }
      @media (max-width: $breakpoint1200) {
        max-width: 520px;
        width: 100%;
      }
    }
  }

  &__title {
    font-size: 96px;
    line-height: 96px;
    letter-spacing: 0.01em;
    margin-bottom: 40px;
    font-weight: 700;
    @media (max-width: 1600px) {
      font-size: 76px;
      line-height: 76px;
      letter-spacing: 0.01em;
    }
    @media (max-width: $breakpoint768) {
      font-size: 56px;
      line-height: 56px;
    }
    @media (max-width: $breakpoint576) {
      font-size: 36px;
      line-height: 36px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    gap: 24px 46px;
  }

  &__item {
    max-width: 405px;
    &:nth-child(even) {
      max-width: 452px;
    }
    @media (max-width: $breakpoint992) {
      width: calc(50% - 23px);
    }
    @media (max-width: $breakpoint576) {
      width: calc(100% - 23px);
      max-width: 100%;
      &:nth-child(even) {
        max-width: 100%;
      }
    }
  }

  &__item-title {
    font-weight: 700;
    font-size: 29px;
    line-height: 42px;
    margin-bottom: 10px;
    @media (max-width: $breakpoint576) {
      font-size: 22px;
    }
  }

  &__item-text {
    @media (max-width: $breakpoint992) {
      font-size: 16px;
      line-height: 26px;
    }
  }
}
