body {
  font-family: "DroidSerif", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.33;
  text-size-adjust: 100%;
  color: $primary;
  &.lock {
    overflow: hidden;
  }
}
// :focus {
//   outline: #08f auto 2px;
// }

.hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}
.main {
  flex: 1 1 auto;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.visibility-hidden-2 {
  clip: rect(1px 1px 1px 1px);
  border: 0;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  white-space: nowrap;
  width: 1px;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1350px;
  width: 100%;

  // @media (max-width: $breakpoint1400) {
  //   max-width: 1190px;
  // }
  // @media (max-width: $breakpoint1200) {
  //   max-width: 960px;
