.plans {
  padding: 72px 0 141px 0;
  &__title {
    font-weight: 700;
    font-size: 96px;
    line-height: 96px;
    text-align: center;
    letter-spacing: 0.01em;
    margin-bottom: 28px;
    @media (max-width: $breakpoint992) {
      font-size: 76px;
      line-height: 76px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 51px;
      line-height: 56px;
    }
  }

  &__inner {
    display: flex;
    gap: 36px;
    @media (max-width: $breakpoint1400) {
      gap: 15px;
    }
    @media (max-width: $breakpoint1200) {
      flex-wrap: wrap;
      gap: 26px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    padding: 28px;
    width: calc(25% - 27px);
    background: #ffffff;
    box-shadow: 0px 4px 52px rgba(36, 41, 83, 0.06);
    border-radius: 10px;
    align-items: flex-start;
    @media (max-width: $breakpoint1400) {
      width: calc(25% - 11px);
    }
    @media (max-width: $breakpoint1200) {
      width: calc(50% - 13px);
    }
    @media (max-width: $breakpoint768) {
      width: 100%;
      align-items: stretch;
    }
  }

  &__label {
    display: inline-flex;
    color: $secondary;
    background: rgba(0, 102, 255, 0.1);
    border-radius: 10px;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.01em;
    margin-bottom: 12px;
    padding: 8px;
    width: 127px;
  }

  &__name {
    font-size: 27px;
    line-height: 37px;
    font-weight: 700;
    letter-spacing: 0.01em;
    margin-bottom: 24px;
  }

  &__list {
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.01em;
    margin-bottom: 52px;
  }

  &__li {
    border-top: 1px solid #dae0eb;
    padding: 24px 0;
  }

  &__btn {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.01em;
    margin-top: auto;
    @media (max-width: $breakpoint1200) {
      max-width: none;
    }
  }
}
