.footer {
  padding: 30px 0;

  &__inner {
    display: flex;
    justify-content: flex-end;
    margin-right: 210px;
    @media (max-width: $breakpoint1200) {
      justify-content: center;
      margin: 0;
    }
  }
  &__block {
    width: 500px;
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 52px;
    letter-spacing: 0.01em;
    margin-bottom: 20px;
    @media (max-width: $breakpoint576) {
      font-size: 37px;
      line-height: 42px;
    }
  }

  &__box {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 57px;
    column-gap: 25px;
    row-gap: 18px;
  }

  &__box-item {
  }

  &__copy {
    display: flex;
    flex-direction: column;
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.01em;
    gap: 7px;
  }
}
