.about {
  &__inner {
  }

  &__item {
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    padding: 40px 50px;
    border-radius: 40px;
    background: url(./../img/about-bg.png) no-repeat center/cover;
    &:not(:last-child) {
      margin-bottom: 37px;
    }
    &:before {
      content: "";
      opacity: 0.4;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url(./../img/about-bg-effect.png) no-repeat center/cover;
    }
    @media (max-width: $breakpoint992) {
      flex-direction: column;
    }
    @media (max-width: $breakpoint576) {
      padding: 20px 30px;
    }
  }
  &__item--about {
    background-color: #0066ff;
  }
  &__item--trade {
    background-color: #4a586c;
  }
  &__item--aff {
    background-color: #871f7d;
  }

  &__item-title {
    font-size: 64px;
    line-height: 68px;
    letter-spacing: 0.01em;
    color: $white;
    margin-right: 44px;
    width: 100%;
    max-width: 308px;
    @media (max-width: $breakpoint992) {
      margin: 0 0 40px 0;
      width: auto;
      font-size: 54px;
      line-height: 58px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 44px;
      line-height: 48px;
    }
  }

  &__item-text {
    margin-right: 70px;
    color: $white;
    font-size: 22px;
    line-height: 1.27;
    letter-spacing: 0.01em;
    max-width: 631px;
    width: 100%;
    @media (max-width: $breakpoint992) {
      margin: 0 0 50px 0;
      max-width: fit-content;
    }
    @media (max-width: $breakpoint768) {
      margin: 0 0 20px 0;
      max-width: fit-content;
      font-size: 18px;
      line-height: 26px;
    }
  }

  &__item-ico {
    width: 180px;
    height: 174px;
    img {
      width: auto;
      max-width: fit-content;
    }
  }
}
