  box-shadow: 0px 8px 21px rgba(0, 102, 255, 0.39);
  border-radius: 10px;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.01em;
  color: white;
  transition: 0.2s ease-in-out;
}
.btn:hover {
  background: rgba(0, 102, 255, 0.8549019608);
}

.btn--purple {
  background: #9647bb;
}
.btn--purple:hover {
  background: rgba(150, 71, 255, 0.8549019608);
}

