.nav.active .nav__list {
  display: flex;
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
}
.nav__list {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 6px;
}
@media (max-width: 991.98px) {
  .nav__list {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transform: translateX(100%);
    transition: 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    background-color: #fff;
    font-size: 26px;
    gap: 20px;
  }
}
.nav__item {
  padding: 6px 10px;
  transition: 0.3s ease-out;
}
.nav__item:hover {
  color: #0066ff;
  background: rgba(0, 102, 255, 0.11);
  border-radius: 4px;
}
.burger {
  display: none;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  cursor: pointer;
  margin-left: 20px;
}
@media (max-width: 991.98px) {
  .burger {
    display: flex;
  }
}
.burger.active .burger__line {
  background: 0 0;
}
.burger.active .burger__line::before {
  top: 0;
  transform: rotate(45deg);
}
.burger.active .burger__line::after {
  top: 0;
  transform: rotate(-45deg);
}
.burger__line {
  display: block;
  width: 26px;
  height: 3px;
  background-color: #242953;
  transition: all 0.3s ease-in-out;
  position: relative;
}
.burger__line::before, .burger__line::after {
  content: "";
  left: 0;
  position: absolute;
  width: 26px;
  height: 3px;
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger__line::before {
  top: -9px;
}
.burger__line::after {
  bottom: -9px;
}

