.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 336px;
  width: 100%;
  height: 56px;
  text-align: center;
  background: $secondary;
  box-shadow: 0px 8px 21px rgba(0, 102, 255, 0.39);
  border-radius: 10px;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.01em;
  color: white;
  transition: 0.2s ease-in-out;
  &:hover {
    background: #0066ffda;
  }
}
.btn--purple {
  background: #9647bb;
  &:hover {
    background: #9647ffda;
  }
}

.home {
  padding-top: 62px;
  background: #f3f9fe;
  border-radius: 0px 0px 100px 100px;
}
