.header {
  padding: 13px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: $white;
  z-index: 101;
  &__inner {
    display: flex;
    align-items: center;
    @media (max-width: $breakpoint992) {
      justify-content: space-between;
    }
  }
}

.logo {
  margin-right: 18px;
}
.nav {
  &.active &__list {
    display: flex;
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
  &__list {
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 6px;
    @media (max-width: $breakpoint992) {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      transform: translateX(100%);
      transition: 0.3s ease-in-out;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      background-color: $white;
      font-size: 26px;
      gap: 20px;
    }
  }

  &__item {
    padding: 6px 10px;
    transition: 0.3s ease-out;
    &:hover {
      color: $secondary;
      background: rgba(0, 102, 255, 0.11);
      border-radius: 4px;
    }
  }

  &__link {
  }
}

.burger {
  display: none;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  cursor: pointer;
  margin-left: 20px;

  @media (max-width: $breakpoint992) {
    display: flex;
  }

  &.active {
    .burger__line {
      background: 0 0;
    }

    .burger__line::before {
      top: 0;
      transform: rotate(45deg);
    }

    .burger__line::after {
      top: 0;
      transform: rotate(-45deg);
    }
  }

  &__line {
    display: block;
    width: 26px;
    height: 3px;
    background-color: $primary;
    transition: all 0.3s ease-in-out;
    position: relative;

    &::before,
    &::after {
      content: "";
      left: 0;
      position: absolute;
      width: 26px;
      height: 3px;
      background-color: currentColor;
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::before {
      top: -9px;
    }

    &::after {
      bottom: -9px;
    }
  }
}
