.ask {
  padding-bottom: 91px;
  &__title {
    font-size: 86px;
    line-height: 86px;
    text-align: center;
    letter-spacing: 0.01em;
    font-weight: 700;
    margin-bottom: 30px;
    @media (max-width: $breakpoint992) {
      font-size: 66px;
      line-height: 66px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 56px;
      line-height: 56px;
    }
    @media (max-width: $breakpoint576) {
      font-size: 51px;
      line-height: 51px;
    }
  }

  &__acc {
  }

  &__acc-item {
    padding: 23px 29px;
    cursor: pointer;
    background: #f3f9fe;
    border-radius: 10px;
    &:not(:last-child) {
      margin-bottom: 24px;
    }
    @media (max-width: $breakpoint576) {
      padding: 17px 20px;
    }
  }

  &__acc-title {
    font-size: 24px;
    line-height: 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    letter-spacing: 0.01em;

    svg {
      margin-left: 10px;
      flex: 0 0 auto;
      transition: 0.3s ease;
      transform: rotate(45deg);
    }
    @media (max-width: $breakpoint768) {
      font-size: 20px;
      line-height: 24px;
    }
    @media (max-width: $breakpoint576) {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__acc-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s cubic-bezier(0, 1, 0.5, 1) 0s;
    span {
      padding-top: 13px;
      display: inline-flex;
    }
    width: 90%;
  }
}

.active {
  .ask__acc-content {
    max-height: 1000px;
    transition: max-height 0.5s cubic-bezier(1, 0, 1, 0.5) 0s;
  }
  .ask__acc-title {
    svg {
      transform: rotate(0);
    }
  }
}
