.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 137px 0 92px 0;
  @media (max-width: $breakpoint1200) {
    padding: 107px 0 92px 0;
  }
  @media (max-width: $breakpoint768) {
    padding: 69px 0 92px 0;
  }
  @media (max-width: $breakpoint576) {
    padding: 49px 0 92px 0;
  }
  &__inner {
    display: flex;

    gap: 203px;
    @media (max-width: $breakpoint1200) {
      gap: 123px;
    }
    @media (max-width: $breakpoint992) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }

  &__item {
    &:nth-child(1) {
      max-width: 549px;
      width: 100%;
      margin-top: 81px;
      @media (max-width: $breakpoint768) {
        max-width: 100%;
      }
    }
    &:nth-child(2) {
      max-width: 392px;
      width: 100%;
      @media (max-width: $breakpoint992) {
        max-width: 549px;
      }
      @media (max-width: $breakpoint768) {
        max-width: 100%;
      }
    }
  }

  &__title {
    font-weight: 700;
    font-size: 59px;
    line-height: 59px;
    margin-bottom: 24px;
    @media (max-width: $breakpoint1200) {
      font-size: 52px;
      line-height: 56px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 48px;
      line-height: 54px;
    }
    @media (max-width: $breakpoint576) {
      font-size: 41px;
      line-height: 50px;
    }
  }

  &__text {
    margin-bottom: 53px;
  }

  &__btns {
    display: flex;
    flex-direction: column;
    gap: 21px;
    svg {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 31px;
    }
  }
  &__btn {
    @media (max-width: $breakpoint576) {
      max-width: initial;
    }
  }
}

.calc {
  &__form {
    padding: 28px;
    background: $white;
  }

  &__title {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 24px;
    @media (max-width: $breakpoint576) {
      font-size: 24px;
      line-height: 29px;
      text-align: center;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    gap: 18px;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.01em;
  }

  &__body-item {
    position: relative;
    border: 1px solid #dae0eb;
    border-radius: 10px;
    padding: 17px 23px;

    &:first-child {
      padding: 0;
      border: 0;
    }

    span {
      display: inline-flex;
      justify-content: center;
      background: $white;
      color: #859fcf;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.01em;
      position: absolute;
      top: -8px;
      left: 12px;
      z-index: 1;
      padding: 0 5px;
    }
  }

  &__select {
    font-size: 46px;
    line-height: 20px;
    letter-spacing: 0.01em;
    position: relative;
  }

  &__res {
  }

  &__btn {
    @media (max-width: $breakpoint576) {
      max-width: inherit;
    }
  }
}

.choices__inner {
  background: none;
  border: 1px solid #dae0eb;
  padding: 17px 23px !important;
  border-radius: 10px;
  font-size: 16px;
  line-height: 20px;
  @media (max-width: $breakpoint576) {
    padding: 13px 19px !important;
  }
}
.is-open .choices__inner {
  border-radius: 10px 10px 0 0;
}

.choices__list--single {
  padding: 0;
}
.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background: #0066ffe2;
  color: $white;
}
.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  font-size: 16px;
  line-height: 20px;
  padding: 17px;
  z-index: 1;
  position: relative;
}

.choices[data-type*="select-one"]::after {
  display: none;
}
.choices__list--dropdown .choices__item--selectable.is-highlighted::after,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted::after {
  display: none;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  z-index: 150;
}
